import React, { useEffect, useState } from 'react'
import { Button, Spin } from 'antd'
import { connect } from 'react-redux'
import { getDataAction, updateUserAction } from '../../redux/dataDuck'
import text from '../text'
import { Link } from 'react-router-dom'

const FAQ = ({info, dataUpdated, history, fetching, language, getData, updateUser}) => {

    let handleSubmit = typeInput => {

    }

    useEffect(() => {
        getData()
    }, [])

    return (
        <div className='application-container'>
             {
                fetching ? 
                    <div className='spinner-full-page'><Spin size='large'/></div>
                    :
                    <div className="application-content">
                        <div>
                            <div className="app-top-icon">
                                <img src="/img/sidebar/faq-gray.svg" alt="profile"/>
                                <p>{text.sidebar.faq[language]}</p>
                            </div>
                        </div>
                        <div className='padded-app-container form-container'>
                            <div className="propietary-cards-container">
                                <Link to='/faq/faq1' className="propietary-card">
                                    <img src="/img/propietary/housecleaning.svg" alt="propietary"/>
                                    <p>{text.propietaryServices.houseCleaning[language]}</p>
                                </Link>
                                <Link to='/faq/faq2' className="propietary-card">
                                    <img src="/img/propietary/electricity.svg" alt="propietary"/>
                                    <p>{text.propietaryServices.manteinanceServices[language]}</p>
                                </Link>
                                <Link to='/faq/faq3' className="propietary-card">
                                    <img src="/img/propietary/cableinternet.svg" alt="propietary"/>
                                    <p>{text.propietaryServices.cable[language]}</p>
                                </Link>
                                <Link to='/faq/faq4' className="propietary-card">
                                    <img src="/img/propietary/trash.svg" alt="propietary"/>
                                    <p>{text.propietaryServices.publicServices[language]}</p>
                                </Link>
                                <Link to='/faq/faq5' className="propietary-card">
                                    <img src="/img/propietary/interiordesign.svg" alt="propietary"/>
                                    <p>{text.propietaryServices.interiorDesign[language]}</p>
                                </Link>
                            </div>
                        </div>
                    </div>
            }
        </div>
    )
}

let mapState = ({
    app: {language},
    user: {fetching},
    data: {info, dataUpdated}
}) => ({
    language,
    fetching,
    info,
    dataUpdated
})

export default connect(
    mapState,
    {
        getData: getDataAction,
        updateUser: updateUserAction
    }
)(FAQ)
