import React, { useEffect, useState } from 'react'
import { Button, Input, Spin } from 'antd'
import { connect } from 'react-redux'
import { getDataAction, handleInfoAction, updateUserAction } from '../../../redux/dataDuck'
import text from '../../text'
import { Link } from 'react-router-dom'

const FinancialForm = ({info, dataUpdated, history, fetching, language, getData, updateUser, handleInfo}) => {

    useEffect(() => {
        getData()
    }, [])

    return (
        <div className='application-container'>
             {
                fetching ? 
                    <div className='spinner-full-page'><Spin size='large'/></div>
                    :
                    <div className="application-content no-justify">
                        <div>
                            <div className="app-top-icon">
                                <img src="/img/sidebar/finance.svg" alt="profile"/>
                                <p>{text.sidebar.financing[language]}</p>
                            </div>
                            <h2 className='small-title'>{text.financingForm.title[language]}</h2>
                        </div>
                        <div className='padded-app-container form-container condensed-inputs'>
                            <p className='finance-form-subtitle1'>{text.financingForm.fillInputs[language]}</p>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.officePhone} name='officePhone' placeholder={text.financingForm.officePhone[language]}/>
                                <Input onChange={handleInfo} value={info.homePhone} name='homePhone' placeholder={text.financingForm.homePhone[language]}/>
                                <Input onChange={handleInfo} value={info.rfc} name='rfc' placeholder={text.financingForm.rfc[language]}/>
                                <Input onChange={handleInfo} value={info.cellPhone} name='cellPhone' placeholder={text.financingForm.cellPhone[language]}/>
                            </div>
                            <div className="multiple-inputs">
                                <Input disabled value={info.email} name='email' placeholder={text.financingForm.email[language]}/>
                                <Input onChange={handleInfo} value={info.livingTime} name='livingTime' placeholder={text.financingForm.livingTime[language]}/>
                                <Input onChange={handleInfo} value={info.occupation} name='occupation' placeholder={text.financingForm.occupation[language]}/>
                            </div>
                            <p className='finance-form-subtitle2'>{text.financingForm.livesInHouse[language]}</p>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.address} name='address' placeholder={text.financingForm.address[language]}/>
                            </div>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.companyName} name='companyName' placeholder={text.financingForm.companyName[language]}/>
                                <Input onChange={handleInfo} value={info.companyField} name='companyField' placeholder={text.financingForm.companyField[language]}/>
                            </div>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.companyAddress} name='companyAddress' placeholder={text.financingForm.companyAddress[language]}/>
                                <Input onChange={handleInfo} value={info.companyTime} name='companyTime' placeholder={text.financingForm.companyTime[language]}/>
                            </div>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.functionsDescription} name='functionsDescription' placeholder={text.financingForm.functionsDescription[language]}/>
                            </div>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.companyRole} name='companyRole' placeholder={text.financingForm.companyRole[language]}/>
                                <Input onChange={handleInfo} value={info.academicLevel} name='academicLevel' placeholder={text.financingForm.academicLevel[language]}/>
                                <Input onChange={handleInfo} value={info.economicDependants} name='economicDependants' placeholder={text.financingForm.economicDependants[language]}/>
                            </div>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.height} name='height' placeholder={text.financingForm.height[language]}/>
                                <Input onChange={handleInfo} value={info.weight} name='weight' placeholder={text.financingForm.weight[language]}/>
                                <Input onChange={handleInfo} value={info.ssn} name='ssn' placeholder={text.financingForm.ssn[language]}/>
                            </div>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.carBrand} name='carBrand' placeholder={text.financingForm.carBrand[language]}/>
                                <Input onChange={handleInfo} value={info.carValue} name='carValue' placeholder={text.financingForm.carValue[language]}/>
                            </div>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.realStateNumber} name='realStateNumber' placeholder={text.financingForm.realStateNumber[language]}/>
                                <Input onChange={handleInfo} value={info.realStateValue} name='realStateValue' placeholder={text.financingForm.realStateValue[language]}/>
                            </div>
                            <p className='finance-form-subtitle1'>{text.financingForm.referenceTitle[language]}</p>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.contact1Name} name='contact1Name' placeholder={text.financingForm.contactName[language]}/>
                                <Input onChange={handleInfo} value={info.contact1Address} name='contact1Address' placeholder={text.financingForm.contactAddress[language]}/>
                                <Input onChange={handleInfo} value={info.contact1Phone} name='contact1Phone' placeholder={text.financingForm.contactPhone[language]}/>
                            </div>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.contact2Name} name='contact2Name' placeholder={text.financingForm.contactName[language]}/>
                                <Input onChange={handleInfo} value={info.contact2Address} name='contact2Address' placeholder={text.financingForm.contactAddress[language]}/>
                                <Input onChange={handleInfo} value={info.contact2Phone} name='contact2Phone' placeholder={text.financingForm.contactPhone[language]}/>
                            </div>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.contact3Name} name='contact3Name' placeholder={text.financingForm.contactName[language]}/>
                                <Input onChange={handleInfo} value={info.contact3Address} name='contact3Address' placeholder={text.financingForm.contactAddress[language]}/>
                                <Input onChange={handleInfo} value={info.contact3Phone} name='contact3Phone' placeholder={text.financingForm.contactPhone[language]}/>
                            </div>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.contact4Name} name='contac4tName' placeholder={text.financingForm.contactName[language]}/>
                                <Input onChange={handleInfo} value={info.contact4Address} name='contactAd4dress' placeholder={text.financingForm.contactAddress[language]}/>
                                <Input onChange={handleInfo} value={info.contact4Phone} name='contact4Phone' placeholder={text.financingForm.contactPhone[language]}/>
                            </div>
                        </div>
                        <div className="multiple-buttons small-buttons">
                            <Button className='nav-button'>{text.button.save[language]}</Button>
                            <Button className='nav-button'>{text.button.preview[language]}</Button>
                            <Button className='nav-button'>{text.button.print[language]}</Button>
                        </div>
                    </div>
            }
        </div>
    )
}

let mapState = ({
    app: {language},
    user: {fetching},
    data: {info, dataUpdated}
}) => ({
    language,
    fetching,
    info,
    dataUpdated
})

export default connect(
    mapState,
    {
        getData: getDataAction,
        updateUser: updateUserAction,
        handleInfo: handleInfoAction
    }
)(FinancialForm)
