import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { getDataAction, getHouseDataAction } from '../../redux/dataDuck'
import { Button, Input, Spin } from 'antd'
import { Link } from 'react-router-dom'
import text from '../text'
import { BuyHouseMap } from './buyHouseComponents/BuyHouseMap'

const BuyHouse = ({history,info, language, fetchingData, fetchingHouse, house, getHouse, getData}) => {


    let handleSubmit = () => {
        history.push('/data')
    }

    useEffect(() => {
        getHouse()
        getData()
    }, [])

    return (
        <div className='application-container'>
            {
                fetchingData || fetchingHouse ? 
                    <div className='spinner-full-page'><Spin size='large'/></div>
                :
                    <div className="application-content no-justify">
                        <div>
                            <div className="app-top-icon">
                                <img src="/img/sidebar/buy.svg" alt="profile"/>
                                <p>{text.sidebar.buyHouse[language]}</p>
                            </div>
                            <h2>{text.sidebarSubtitle.buyHouse[language]}</h2>
                        </div>
                        <div className='padded-app-container form-container'>
                            <div className="multiple-inputs map-inputs">
                                <Input disabled value={house.lotNumber}/>
                                <Input disabled value={house.lotSize} suffix={language === 'english' ? <small>yd<sup>2</sup></small> : <small>m<sup>2</sup></small>}/>
                                <Input disabled value={info.lotModel} />
                            </div>
                        </div>
                        <BuyHouseMap language={language} house={house}/>
                        <div className='multiple-buttons'>
                            <Link to='/choose'><Button loading={false} className='nav-button'>{text.button.returnCaps[language]}</Button></Link>
                            <Button onClick={handleSubmit} loading={false} className='nav-button'>{text.button.approve[language]}</Button>
                        </div>
                    </div>
            }
        </div>
    )
}

let mapState = ({
    app: {language},
    data: {fetchingData, info, house, fetchingHouse}
}) => ({
    language,
    fetchingData,
    fetchingHouse,
    house,
    info
})

export default connect(
    mapState,
    {
        getData: getDataAction,
        getHouse: getHouseDataAction
    }
)(BuyHouse)
