import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { changeLangAction } from '../../redux/appDuck'
import { logoutAction } from '../../redux/userDuck'
import { withRouter } from 'react-router-dom'
import text from '../text'

const AuthNav = ({location, language, info, logout}) => {

    return (
        <div className='auth-nav'>
            <div className="top-nav">
                <img src="/img/navbar/lupa.svg" alt="lupa"/>
                <img src="/img/navbar/notify.svg" alt="notify"/>
                <img onClick={logout} src="/img/navbar/profile.svg" alt="profile"/>
            </div>
            <div className="left-nav">
                <img className='sidebar-logo' src="/img/common/logo-dark.png" alt="PP logo black"/>
                <div className="sidebar-nav">
                    <Link to='/profile' className={`sidebar-element${location.pathname === '/profile' || location.pathname === '/' ? '-active' : ''}`}>
                        <img src={`/img/sidebar/profile${location.pathname === '/profile' || location.pathname ===  '/' ? '' : '-gray'}.png`} alt="nav element"/>
                        <p>{text.sidebar.profile[language]}</p>
                    </Link>
                    <Link to='/choose' className={`${info.step > 0 ? '' : 'link-disabled'} sidebar-element${location.pathname === '/choose' ? '-active' : ''}`}>
                        <img src={`/img/sidebar/choose${location.pathname === '/choose' ? '' : '-gray'}.png`} alt="nav element"/>
                        <p>{text.sidebar.chooseHouse[language]}</p>
                    </Link>
                    {
                        info.wantFinance &&
                            <Link to='/financing' className={`${info.step > 1 ? '' : 'link-disabled'} sidebar-element${location.pathname.indexOf('/financing') !== -1 ? '-active' : ''}`}>
                                <img src={`/img/sidebar/finance${location.pathname.indexOf('/financing') !== -1 ? '' : '-gray'}.svg`} alt="nav element"/>
                                <p>{text.sidebar.financing[language]}</p>
                            </Link>
                    }
                    <Link to='/buy' className={`${info.step > 2 ? '' : 'link-disabled'} sidebar-element${location.pathname === '/buy' ? '-active' : ''}`}>
                        <img src={`/img/sidebar/buy${location.pathname === '/buy' ? '' : '-gray'}.svg`} alt="nav element"/>
                        <p>{text.sidebar.buyHouse[language]}</p>
                    </Link>
                    <Link to='/data' className={`${info.step > 3 ? '' : 'link-disabled'} sidebar-element${location.pathname === '/data' ? '-active' : ''}`}>
                        <img src={`/img/sidebar/data${location.pathname === '/data' ? '' : '-gray'}.svg`} alt="nav element"/>
                        <p>{text.sidebar.data[language]}</p>
                    </Link>
                    <Link to='/documentation' className={`${info.step > 4 ? '' : 'link-disabled'} sidebar-element${location.pathname === '/documentation' ? '-active' : ''}`}>
                        <img src={`/img/sidebar/documents${location.pathname === '/documentation' ? '' : '-gray'}.svg`} alt="nav element"/>
                        <p>{text.sidebar.documentation[language]}</p>
                    </Link>
                    <Link to='/interior-design' className={`${info.step > 5 ? '' : 'link-disabled'} sidebar-element${location.pathname === 'interior-design' ? '-active' : ''}`}>
                        <img src={`/img/sidebar/intDesign${location.pathname === '/interior-design' ? '' : '-gray'}.svg`} alt="nav element"/>
                        <p>{text.sidebar.interiorDesign[language]}</p>
                    </Link>
                    <Link to='propietary-service' className={`sidebar-element${location.pathname === 'propietary-service' ? '-active' : ''}`}>
                        <img src={`/img/sidebar/ownerServ${location.pathname === '/propietary-service' ? '' : '-gray'}.svg`} alt="nav element"/>
                        <p>{text.sidebar.propietaryService[language]}</p>
                    </Link>
                    <Link to='/faq' className={`sidebar-element${location.pathname.indexOf('/faq') !== -1 ? '-active' : ''}`}>
                        <img src={`/img/sidebar/faq${location.pathname.indexOf('/faq') !== -1 ? '' : '-gray'}.svg`} alt="nav element"/>
                        <p>{text.sidebar.faq[language]}</p>
                    </Link>
                    <Link to='/help' className={`sidebar-element${location.pathname === '/help' ? '-active' : ''}`}>
                        <img src={`/img/sidebar/help${location.pathname === '/help' ? '' : '-gray'}.svg`} alt="nav element"/>
                        <p>{text.sidebar.help[language]}</p>
                    </Link>
                </div>
            </div>
        </div>
    )
}

let mapState = ({
    app: {language},
    data: {info}
}) => ({
    language,
    info
})

export default connect(
    mapState,
    {
        toggleLanguage: changeLangAction,
        logout: logoutAction
    }
)(withRouter(AuthNav))
