import React, { useEffect, useState } from 'react'
import { Button, Select, Spin } from 'antd'
import { connect } from 'react-redux'
import { getDataAction, updateUserAction } from '../../redux/dataDuck'
import text from '../text'
import { Link } from 'react-router-dom'
import ReactImageMagnify from 'react-image-magnify'
let { Option } = Select

const InteriorDesign = ({info, dataUpdated, history, fetching, language, getData, updateUser}) => {

    let handleSubmit = typeInput => {

    }

    useEffect(() => {
        getData()
    }, [])

    return (
        <div className='application-container'>
             {
                fetching ? 
                    <div className='spinner-full-page'><Spin size='large'/></div>
                    :
                    <div className="application-content">
                        <div>
                            <div className="app-top-icon">
                                <img src="/img/sidebar/intDesign.svg" alt="profile"/>
                                <p>{text.sidebar.interiorDesign[language]}</p>
                            </div>
                        <h2>{text.sidebarSubtitle.interiorDesign[language]}</h2>
                        </div>
                        <div className='padded-app-container form-container'>
                            <div className="int-design-container">
                                <div className="int-design-left">
                                    <ReactImageMagnify
                                    {...{
                                        smallImage: {
                                            alt: 'Demo',
                                            isFluidWidth: true,
                                            src: '/img/intDesign/design-bg.png'
                                        },
                                        largeImage: {
                                            src: '/img/intDesign/design-bg.png',
                                            width: 1200,
                                            height: 1800
                                        },
                                        enlargedImageContainerDimensions: {
                                            width: 300,
                                            height: '100%'
                                        }
                                    }} 
                                    />
                                </div>
                                <div className="int-design-right">
                                    <div className="int-design-select">
                                        <p>{text.interiorDesign.area[language]}: </p>
                                        <Select
                                            defaultValue='1'
                                        >
                                            <Option value='1'>{text.interiorDesign.kitchen[language]}</Option>
                                            <Option value='2'>{text.interiorDesign.livingRoom[language]}</Option>
                                        </Select>
                                    </div>
                                    <div className="int-design-select">
                                        <p>{text.interiorDesign.options[language]}: </p>
                                        <Select
                                            defaultValue='1'
                                        >
                                            <Option value='1'>{text.interiorDesign.floor[language]}</Option>
                                            <Option value='2'>{text.interiorDesign.walls[language]}</Option>
                                        </Select>
                                    </div>
                                    <div className="sample-container">
                                        <img src="/img/intDesign/floor1.png" alt="floor"/>
                                        <img src="/img/intDesign/floor2.png" alt="floor"/>
                                        <img src="/img/intDesign/floor3.png" alt="floor"/>
                                        <img src="/img/intDesign/floor4.png" alt="floor"/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
            }
        </div>
    )
}

let mapState = ({
    app: {language},
    user: {fetching},
    data: {info, dataUpdated}
}) => ({
    language,
    fetching,
    info,
    dataUpdated
})

export default connect(
    mapState,
    {
        getData: getDataAction,
        updateUser: updateUserAction
    }
)(InteriorDesign)
