import React, { useEffect, useState } from 'react'
import { Button, Collapse, Spin } from 'antd'
import { connect } from 'react-redux'
import { getDataAction, updateUserAction } from '../../../redux/dataDuck'
import text from '../../text'
import { Link } from 'react-router-dom'
let { Panel } = Collapse

const PropietaryDetails = ({info, dataUpdated, history, fetching, language, getData, updateUser}) => {

    let handleSubmit = typeInput => {

    }

    useEffect(() => {
        getData()
    }, [])

    return (
        <div className='application-container'>
             {
                fetching ? 
                    <div className='spinner-full-page'><Spin size='large'/></div>
                    :
                    <div className="application-content">
                        <div>
                            <div className="app-top-icon">
                                <img src="/img/sidebar/ownerServ.svg" alt="profile"/>
                                <p>{text.sidebar.propietaryService[language]}</p>
                            </div>
                        </div>
                        <div className='padded-app-container form-container condensed-inputs'>
                            <div className="accordion-container">
                                <Collapse defaultActiveKey={['1']}>
                                    <Panel header="Empresa 1" key="1">
                                        <p>Nombre del representante</p>
                                        <p>correo@correo.com</p>
                                        <p>Teléfono: xxx-xxx-xxx</p>
                                        <p>http://homepage.com</p>
                                    </Panel>
                                    <Panel header="Empresa 2" key="2">
                                    <p>Nombre del representante</p>
                                        <p>correo@correo.com</p>
                                        <p>Teléfono: xxx-xxx-xxx</p>
                                        <p>http://homepage.com</p>
                                    </Panel>
                                    <Panel header="Empresa 3" key="3">
                                    <p>Nombre del representante</p>
                                        <p>correo@correo.com</p>
                                        <p>Teléfono: xxx-xxx-xxx</p>
                                        <p>http://homepage.com</p>
                                    </Panel>
                                </Collapse>
                            </div>
                            <Link to='/faq'><Button className='nav-button'>{text.button.returnCaps[language]}</Button></Link>
                        </div>
                    </div>
            }
        </div>
    )
}

let mapState = ({
    app: {language},
    user: {fetching},
    data: {info, dataUpdated}
}) => ({
    language,
    fetching,
    info,
    dataUpdated
})

export default connect(
    mapState,
    {
        getData: getDataAction,
        updateUser: updateUserAction
    }
)(PropietaryDetails)
