import React, { useEffect, useState } from 'react'
import { Button, message, Spin, Upload } from 'antd'
import { connect } from 'react-redux'
import { getDataAction, handleInfoAction, updateUserAction } from '../../redux/dataDuck'
import text from '../text'
import { Link } from 'react-router-dom'

let { Dragger } = Upload

const Documentation = ({info, fetching, dataUpdated, language, getData, handleInfo, updateUser}) => {

    let [uploaded, setUploaded] = useState({
        firstDoc: false,
        secondDoc: false,
        thirdDoc: false
    })

    let handleUpload = (info, number) => {
        console.log(info)
        const { status } = info.file;
        console.log(status)
        if (status !== 'uploading') {
            console.log(info.file, info.fileList);
        }
        if (status === 'done') {
            message.success(`${info.file.name}`);
        } else if (status === 'error') {
            message.error(`${info.file.name}`);
        }
        switch(number){
            case 'firstDoc':
                return setUploaded({...uploaded, firstDoc: true})
            case 'secondDoc':
                return setUploaded({...uploaded, secondDoc: true})
            case 'thirdDoc':
                return setUploaded({...uploaded, thirdDoc: true})
            default:
                return
        }
    }

    useEffect(() => {
        getData()
    }, [])

    return (
        <div className='application-container'>
             {
                fetching ? 
                    <div className='spinner-full-page'><Spin size='large'/></div>
                    :
                    <div className="application-content no-justify">
                        <div>
                            <div className="app-top-icon">
                                <img src="/img/sidebar/documents.svg" alt="profile"/>
                                <p>{text.sidebar.documentation[language]}</p>
                            </div>
                            <h2>{text.sidebarSubtitle.documentation[language]}</h2>
                        </div>
                        <div className='padded-app-container form-container'>
                            <div className="documents-box-container">
                                <div className="doc-box">
                                    <Dragger
                                        accept='image/png, image/jpeg, .pdf'
                                        onChange={info => handleUpload(info, 'firstDoc')}
                                        fileList={[]}
                                    >
                                        <div className='doc-box-elements'>
                                            <p className='doc-box-title'>{text.documentation.firstDoc.title[language]}</p>
                                            <img src={`/img/documentation/upload${uploaded.firstDoc ? 'ed' : ''}.png`} alt="upload"/>
                                            <p className='doc-box-instructions'>{text.documentation.firstDoc.inst[language]}</p>
                                            <p className='doc-box-mb'>{text.documentation.mbText[language]}</p>
                                            <Button className='upload-button'> <img src='/img/documentation/folder.svg' alt='folder'/> {text.documentation.blowseButon[language]}</Button>
                                        </div>
                                    </Dragger>
                                </div>
                                <div className="doc-box">
                                    <Dragger
                                        accept='image/png, image/jpeg, .pdf'
                                        onChange={info => handleUpload(info, 'secondDoc')}
                                        fileList={[]}
                                    >
                                        <div className='doc-box-elements'>
                                            <p className='doc-box-title'>{text.documentation.secondDoc.title[language]}</p>
                                            <img src={`/img/documentation/upload${uploaded.secondDoc ? 'ed' : ''}.png`} alt="upload"/>
                                            <p className='doc-box-instructions'>{text.documentation.secondDoc.inst[language]}</p>
                                            <p className='doc-box-mb'>{text.documentation.mbText[language]}</p>
                                            <Button className='upload-button'> <img src='/img/documentation/folder.svg' alt='folder'/> {text.documentation.blowseButon[language]}</Button>
                                        </div>
                                    </Dragger>
                                </div>
                                <div className="doc-box">
                                    <Dragger
                                        accept='image/png, image/jpeg, .pdf'
                                        onChange={info => handleUpload(info, 'thirdDoc')}
                                        fileList={[]}
                                    >
                                        <div className='doc-box-elements'>
                                            <p className='doc-box-title'>{text.documentation.thirdDoc.title[language]}</p>
                                            <img src={`/img/documentation/upload${uploaded.thirdDoc ? 'ed' : ''}.png`} alt="upload"/>
                                            <p className='doc-box-instructions'>{text.documentation.thirdDoc.inst[language]}</p>
                                            <p className='doc-box-mb'>{text.documentation.mbText[language]}</p>
                                            <Button className='upload-button'> <img src='/img/documentation/folder.svg' alt='folder'/> {text.documentation.blowseButon[language]}</Button>
                                        </div>
                                    </Dragger>
                                </div>
                            </div>
                        </div>
                        <div className="bottom-buttons multiple-buttons">
                            <Link to='/data'><Button className='nav-button'>{text.button.returnCaps[language]}</Button></Link>
                            <Button loading={fetching} className='nav-button'>{text.button.continue[language]}</Button>
                        </div>
                    </div>
            }
        </div>
    )
}

let mapState = ({
    app: {language},
    user: {fetching},
    data: {info, dataUpdated}
}) => ({
    language,
    fetching,
    info,
    dataUpdated
})

export default connect(
    mapState,
    {
        getData: getDataAction,
        handleInfo: handleInfoAction,
        updateUser: updateUserAction
    }
)(Documentation)
